html{
    margin:0;
    background:rgb(0, 0, 0);
    
}
body{
    

    background:rgb(0, 0, 0);

}
#heading{
padding-top: 10vh; 
padding-bottom: 5vh;
text-align: center;

}

.container {
    max-width: 740px;
    font-family: Orbitron , sans-serif;
    border-radius :20px 0 ;
    margin: 0  auto;
    background: rgb(0, 0, 0);
    gap: 1rem;
    display: grid;
    grid-template-columns:  1fr 1fr 1fr 1fr;
    grid-template-rows:  repeat(8,1fr) ;
    justify-content:center ;
    justify-self: center;
    min-height: 300px;
    height: 90%;
    width: 90vw;
    touch-action: manipulation;
}
button {
    font-family: Orbitron , sans-serif;
    border-radius :20px 0 ;
    border: none;
    color: rgb(255, 255, 255);
    font-size: 2em;
    font-weight: bolder;
    background-color: #32353a;
}
button:hover{
    color: lawngreen;
    background-color: white;
}

.operator{
    background-color: #628bc3;
}
#display{
    border-radius :20px 0 ;
    margin-top: 1rem;
    padding: 2rem;
    border: solid 2px black;
    grid-column:1 / span 4;
    grid-row:  1 /span 3;
    background:white;
    font-size: 2em;
    overflow-wrap: break-word; 
    
}

    
#seven{

grid-column:1;
grid-row: 5;
}

#eight {
    grid-column:2;
    grid-row: 5;
}


#nine{
    grid-column:3;
    grid-row: 5;
}

#four{
    grid-column:1;
    grid-row: 6;
}
#five{
    grid-column:2;
    grid-row: 6;
}
#six{
    grid-column:3;
    grid-row: 6;
}
#one{
    grid-column:1;
    grid-row: 7;
}
#two{
    grid-column:2;
    grid-row: 7;
}
#three {
    grid-column:3;
    grid-row: 7;

}
#zero {
    grid-column: 1 / span 2;
    grid-row: 8;


}
#decimal{
    grid-column:3;
    grid-row: 8;

}

#equals{
    grid-column:4;
    grid-row: 8;
    color:#628bc3;

}
#add {
    grid-column:4;
    grid-row: 7;

}

#subtract{
    grid-column:4;

    grid-row: 6;

}
#multiply{
    grid-column:4;
    grid-row: 5;

}
#divide {
    grid-column:4;
    grid-row: 4;

}
#clear {
    grid-column:1 / span 2;
    grid-row: 4;
    background-color: #ad3537;

} 

#del{
background-color: #ad3537;
    grid-column:3;
    grid-row:4;
}